import {fetch} from '@/utils/fetch'

/**
 * 工作流 待办 已办 我的 http://10.211.101.55:8092/hmap/api/hrwaitworkflow/query
 */
export const hrworkflow = (params) => fetch({
  url: 'api/hrworkflow/query',
  method: 'post',
  data: params
});

/**
 * 工作流  查看详情 api/hrWorkflowDetail/query
 */
export const hrWorkflowDetail = (params) => fetch({
  url: 'api/hrWorkflowDetail/query',
  method: 'post',
  data: params
});

/**
 * 工作流  同意 api/hrworkflow/submit
 */
export const hrworkflowSubmit = (params) => fetch({
  url: 'api/hrworkflow/submit',
  method: 'post',
  data: params
});

/**
 * 工作流  拒绝 api/hrworkflow/refuse
 */
export const hrworkflowRefuse = (params) => fetch({
  url: 'api/hrworkflow/refuse',
  method: 'post',
  data: params
});



